import React, { useEffect, useRef } from 'react';
import L from 'leaflet';
import 'leaflet/dist/leaflet.css';
import 'leaflet-routing-machine';

const Map = ({ currentLocation, vehicleLocations }) => {
  const mapRef = useRef(null);
  const mapInstance = useRef(null); // Tham chiu n bn 
  const markers = useRef([]); // Tham chiu n cc markers
  const route = useRef(null); // Tham chiu n route

  useEffect(() => {
    if (!mapRef.current) return;

    // Khi to bn  ch mt ln
    if (!mapInstance.current) {
      mapInstance.current = L.map(mapRef.current).setView([currentLocation.lat, currentLocation.lng], 13);

      L.tileLayer('https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png').addTo(mapInstance.current);
    }

    // Xa cc marker c trc khi thm mi
    markers.current.forEach(marker => {
      if (marker) {
        mapInstance.current.removeLayer(marker);
      }
    });

    // Thm markers mi
    markers.current = vehicleLocations.map(location => {
      const marker = L.marker([location.lat, location.lng])
        .addTo(mapInstance.current)
        .bindPopup(`V tr ca xe: Lat: ${location.lat}, Lng: ${location.lng}`);
      return marker;
    });

    // V ng nu c hai v tr u c
    if (currentLocation && vehicleLocations.length > 0) {
      if (route.current) {
        mapInstance.current.removeControl(route.current); // Xa route c
      }

      route.current = L.Routing.control({
        waypoints: [
          L.latLng(currentLocation.lat, currentLocation.lng),
          L.latLng(vehicleLocations[0].lat, vehicleLocations[0].lng),
        ],
        routeWhileDragging: true,
      }).addTo(mapInstance.current);

      // Di chuyn bng ch dn ra ngoi bn  nu mn hnh nh
      const routingContainer = document.querySelector('.leaflet-routing-container');
      const responsiveContainer = document.getElementById('responsive-routing');

      if (routingContainer && responsiveContainer) {
        if (window.innerWidth <= 768) {
          responsiveContainer.appendChild(routingContainer); // Chuyn bng ch dn ra ngoi
        } else {
          mapInstance.current.getContainer().appendChild(routingContainer); // Tr li bng ch dn vo bn 
        }
      }

      // X l s kin thay i kch thc mn hnh
      const handleResize = () => {
        if (routingContainer && responsiveContainer) {
          if (window.innerWidth <= 768) {
            responsiveContainer.appendChild(routingContainer);
          } else {
            mapInstance.current.getContainer().appendChild(routingContainer);
          }
        }
      };

      window.addEventListener('resize', handleResize);

      // Cleanup s kin resize
      return () => {
        window.removeEventListener('resize', handleResize);
      };
    }
  }, [currentLocation, vehicleLocations]);

  return (
    <div style={{ position: 'relative' }}>
      {/* Bn  */}
      <div ref={mapRef} style={{ width: '100%', height: '400px' }}></div>

      {/* Nt Bo ng */}
      <button
        className="bg-red-500 text-white w-full py-2 my-2"
        style={{ marginBottom: '10px' }}
      >
        Bo ng
      </button>

      {/* Container cho bng ch dn khi responsive */}
      <div
        id="responsive-routing"
        style={{
          padding: '10px',
          background: '#fff',
          borderRadius: '8px',
          boxShadow: '0px 2px 8px rgba(0, 0, 0, 0.1)',
        }}
      >
        {/* Bng ch dn s c di chuyn vo y khi mn hnh nh */}
        
      </div>
      
    </div>
    
  );
};

export default Map;
